.container-video {
  width: 1000px;
  height: 760px;
  margin: auto;
  vertical-align: middle;
  overflow: hidden;
}

.video {
  width: 598px;
  height: 358px;
  position: fixed;
  transform: translate(200px, -680px);
  z-index: 10;
  font-size: 30pt;
  background: black;
  border: 1px grey solid;
  resize: none;
  color: white;
  vertical-align: middle;
  text-align: center;
  font-family: RetroComputer;
}
.video:focus {
  outline: -webkit-focus-ring-color auto 0px;
}



